.fr {
  float: right
}
.tl {
  text-align: left;
}
.tc {
  text-align: center
}
.tr {
  text-align: right;
}
.ellipse {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.inline{
